export function onOnceTransitionEnd(ele, transitionEnd, propertyName = 'transform') {
  if (ele) {
    ele.ontransitionend = e => {
      // 
      e.stopImmediatePropagation();
      // 
      if (e.target === ele && e.propertyName === propertyName) {
        ele.ontransitionend = null;
        transitionEnd(e);
      }
    };
  }
  return ele;
}