import buttonPress from './buttonPress';
import { findTarget } from '../util';
export default function tap(e) {
  const {
    contentEl,
    leftEl,
    rightEl,
    _translate
  } = this;
  const {
    sourceEvent,
    currentTarget
  } = e;
  const target = findTarget(sourceEvent, t => t !== currentTarget && t !== contentEl && t !== leftEl && t !== rightEl);
  // 
  if (contentEl && target === contentEl) {
    // this._directionthis._translate
    // this._translatethis._direction
    // 
    if (_translate === 0) {
      this.emit('press', {
        currentTarget: contentEl,
        timestamp: Date.now(),
        sourceEvent: e
      });
    } else {
      this.hide();
    }
  }
  // 
  else if (target === leftEl) {
    buttonPress.apply(this, [e, 'left']);
  }
  // 
  else if (target === rightEl) {
    buttonPress.apply(this, [e, 'right']);
  }
}